.canvas-container {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	overflow: hidden;
	margin: 20px;
	border-radius: 20px;
}

.canvas-backdrop {
	position: absolute;
	background-color: white;
	width: 100%;
	height: 100%;
	z-index: -2;

	-webkit-transition: background-color 2s;
	-o-transition: background-color 2s;
	transition: background-color 2s;
}

#canvas {
	background-color: rgba(0,0,0,0);
}

.string-container {
	position: absolute;
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	pointer-events: none;
	font-size: 48px;
	background-color: rgba(0,0,0,0);
}

.string-text {
	background-color: white;
	text-align: center;
}

.canvas-background-image {
	z-index: -1;
	position: absolute;
	top: 0;
	width: 1000px;
	height: 1000px;
	pointer-events: none;
	opacity: 1;

	-webkit-transition: opacity 2s;
	-o-transition: opacity 2s;
	transition: opacity 2s;
}

.image-hide {
	opacity: 0;
}





